/* Field.css */
.field-container {
    margin: 20px;
  }
  
  .field-table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 10px;
  }
  
  .field-table th, .field-table td {
    padding: 10px;
    text-align: left;
    border-bottom: 1px solid #ddd;
  }
  
  .field-table th {
    background-color: #f2f2f2;
  }
  
  .field-table tbody tr:hover {
    background-color: #f5f5f5;
  }

  .Month{
    display: flex;
    justify-content: center;
    margin: 0px;
  }

  .MonthHeader{
    margin: 0px;
  }

  .TotalSpan{
    font-weight: bold;
    font-size: 20px;
  }
  